<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png" />
    <HelloWorld msg="Welcome to Your Vue.js App" />
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "Home",
  components: {
    HelloWorld
  },
  beforeRouteLeave(to, from, next) {
    const res = window.confirm(
      "Do you really want to leave this page ? The changes will not be saved"
    );
    if (res) {
      next();
    } else {
      next(false);
    }
  }
};
</script>
